import uk from './uk/index'
import en from './en/index'

const defaultLang = 'uk';

let languages = {
	default: defaultLang,
	uk: uk,
	en: en
};

class LanguagePack {

	// –ø—Ä–æ–≤–µ—Ä—è–µ–º, –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç—Å—è –ª–∏ –≤—ã–±—Ä–∞–Ω–Ω—ã–π —è–∑—ã–∫
	static isLangSupported(app, lang) {
		if(languages.hasOwnProperty(lang)) {
			return true
		}
		return false;
	}

	static setLanguages(app) {
		//—Å–Ω–∞—á–∞–ª–∞ —á–∏—Ç–∞–µ–º get –ø–∞—Ä–∞–º–µ—Ç—Ä lang
		if(LanguagePack.isLangSupported(app, app.$route.params.lang)) {
			app.language = app.$route.params.lang;
		} else {
			/* –µ—Å–ª–∏ –≤—ã–±—Ä–∞–Ω–Ω—ã–π —è–∑—ã–∫, –≤–∑—è—Ç—ã–π –∏–∑ get –ø–∞—Ä–∞–º–µ—Ç—Ä–∞ –Ω–µ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç—Å—è, 
			—Ç–æ —Å—á–∏—Ç—ã–≤–∞–µ–º –µ–≥–æ –∏–∑ LocalStorage*/
			let vueLang = LanguagePack.getLocalStorageLang();
			if(LanguagePack.isLangSupported(app, vueLang)) {
				app.language = vueLang;
			} else {// –∏–Ω–∞—á–µ —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º —è–∑—ã–∫ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é
				app.language = defaultLang;
			}
		}
		app.$validator.localize(app.language);
	}

	static getLocalStorageLang() {
		let vueLang = localStorage.getItem('vue-lang');
		if(vueLang) {
			return vueLang;
		} else {
			return defaultLang;
		}
	}

	// –≤–æ–∑–≤—Ä–∞—â–µ–º –æ–±—ä–µ–∫—Ç —Å –ª–æ–∫–∞–ª–∏–∑–∞—Ü–∏–µ–π
	static getLanguages() {
		return languages;
	}
}

export default LanguagePack;


